.edit-container {
  width: auto;
  z-index: 2;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2.4rem;
  margin-bottom: 25rem;
  display: grid;
}

.edit-quiz-questions-sub-title, .edit-quiz-answers-sub-title {
  grid-column: 2;
  margin-top: 2rem;
}

.new-answer-sub-title, .new-question-sub-title {
  grid-column: 2;
  margin-top: 5rem;
}

#new-question-box {
  width: 90%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 4;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.interchangeable-flex {
  place-content: center;
  display: flex;
}

.interchangable-check-box-flex {
  flex-direction: column;
  grid-column: 1 / 4;
  display: flex;
}

.new-interchangable-check-box-flex {
  grid-column: 1 / 4;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: auto;
  justify-content: center;
  display: grid;
}

#new-interchangeable-check-box {
  grid-area: 2 / 2;
  justify-self: center;
  margin-top: 2rem;
}

#question-delete-box {
  width: 3rem;
  height: 1rem;
  text-align: start;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  place-self: center;
  margin-left: 2rem;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  padding-left: .5rem;
  font-family: inherit;
  font-size: 1.3rem;
}

#new-answer-box {
  width: 90%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 4;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.new-answer-box {
  width: 100%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 4;
  margin-top: 2rem;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.edit-questions-container {
  width: 100%;
  display: flex;
}

.list-num {
  align-self: center;
  margin-right: 2rem;
}

.mc-answers-list-num {
  margin-bottom: 2rem;
  margin-right: 2rem;
}

.questions-scroll-container {
  grid-area: 3 / 1 / auto / 4;
}

.answers-scroll-container {
  grid-area: 5 / 1 / auto / 4;
}

.new-answers-scroll-container {
  width: 90%;
  height: 35vh;
  z-index: 0;
  pointer-events: auto;
  visibility: visible;
  grid-column: 1 / 4;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 12rem;
  place-items: center;
  transition: all .5s;
  display: grid;
  position: relative;
  overflow-x: hidden;
  margin-top: .5rem !important;
}

.wrong-answer-box {
  width: 80%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 3;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.right-answer-box {
  width: 80% !important;
}

.mc-answers-scroll-container {
  width: 90%;
  height: 35vh;
  z-index: 0;
  pointer-events: auto;
  visibility: visible;
  background-color: #f5f5f5;
  border-radius: 5%;
  grid-column: 1 / 4;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 12rem;
  place-items: center;
  margin-bottom: 5rem;
  transition: all .5s;
  display: grid;
  overflow-x: hidden;
  box-shadow: 0 0 .4rem #0000002f;
}

#quiz-type-select {
  width: 30%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-area: 1 / 2 / 2;
  margin-top: 2rem;
  font-family: inherit;
  font-size: 1.3rem;
  position: relative;
}

#edit-multiple-choice-input {
  width: 80%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1;
  font-family: inherit;
  font-size: 1.3rem;
}

#edit-quiz-questions-counter {
  width: 70%;
  height: auto;
  grid-column: 3;
  font-size: 16px;
  font-weight: 600;
  align-self: center !important;
}

.edit-container button {
  text-align: center;
  grid-area: 3 / 2;
  margin-bottom: 2rem;
  font-family: inherit;
  position: relative;
  bottom: 15%;
  right: 18%;
}

#new-quiz-name-input {
  width: 30%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-area: 2 / 2 / 3;
  margin-top: 2rem;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
  position: relative;
  top: 30%;
  bottom: 25%;
}

.edit-answer-box {
  width: 100%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 3;
  margin-right: 2rem;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.edit-quiz-grid {
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(auto, 1fr);
  place-items: center;
  row-gap: 2.4rem;
  margin-top: 2rem;
  display: grid;
}

.edit-question-name-box {
  width: 100%;
  height: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 1 / 3;
  margin-right: 2rem;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
}

#edit-quiz-name-box {
  width: 80%;
  height: 5rem;
  text-align: center;
  z-index: 0;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 2;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
  position: relative;
}

.save-btn {
  width: 80%;
  color: #fff;
  background-color: #c040a0;
  border: none;
  border-radius: 10px;
  grid-column: 1;
  place-self: center;
  margin: 1rem;
  padding: .8rem;
  font-size: 1.8rem;
  font-weight: 450;
  text-decoration: none;
  transition: all .2s;
}

.return-arrow {
  height: 4rem;
  width: 4rem;
  color: #000;
  grid-column: 0;
  grid-row: 1;
  place-self: auto start;
  margin-top: 2.3rem;
  margin-left: 2rem;
  position: absolute;
}

